@import './../../App.scss';  //import vars and mixins

.Home__nav{
    display: grid;
    grid-template-columns: auto 1fr;
    text-align: right;
    width: 90%;
    margin: 1rem auto 4rem;
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: .1rem;
    padding: 0 5rem;
    // color: #000;
    top: 10rem;
    z-index: 3;

}.Home__nav_media{
    img{
        height: 1.4rem;
    }
    a{
        padding: 0 .7rem;
    }
}
.Home__nav_pages{
    a{
        padding: 0 1rem;
    }
}
.Home__hidden{
    display: none;
    top: 10rem ;
    opacity: 0;
    @include anim(showHomeHiddenEl, .5s, 0s, ease-in)
}
//stop init anim on go back button
.Home.not_anim{
    .CurrentProjects{
        display: grid;
    }
    .project{
        opacity: 1;
        top: 0;
    }
    .Top{
        h1{
            opacity: 1;
            letter-spacing: .8rem;
        }
        h2{
            opacity: 1;
            letter-spacing: .4rem;
        }
    }
}
.Home.pastProjects_shown{
    .CurrentProjects__btn{
        display: none;
    }
    .Home__hidden{
        display: block;
    }

}
