@import './../../../App.scss';  //import vars and mixins

.Top{
    height: 80vh;
    display: grid;
    align-content: center;
    padding: 10rem 0 0;
    transition: 1.5s cubic-bezier(.17,.67,.45,1.29);
    background-image: url('./../../../media/bgs/grey.jpg');
    margin-bottom: 0;
    .cover{
        background: rgba($color_black_1, .9)
    }
    //anim for iOs
    h1{
        font-weight: 500;
        font-size: 3.5rem;
        margin-bottom: .7rem;
        text-transform: uppercase;
        letter-spacing: 25rem;
        // transform: scale(1,.1);
        opacity: 0;
        animation: mgAnim 1.5s 0.2s ease-out forwards;
        // @include anim(murrayGrantAnim, 1.5s, 0.2s, ease-out, 0.8rem, 3.5rem)
    }
    @keyframes mgAnim{
        60%{

            // transform: scale(1,.5);
        }
        100%{
            opacity: 1;
            letter-spacing: .8rem;
            transform: scale(1,1);
            // font-size: 3.5rem;
        }
    }
    h2{
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: 400;
        opacity: 0;
        // transform: scale(1.2);
        // @include anim(enterpreneurtAnim, 1s, 1.7s, ease-out, 0.4rem)
        animation: enterpreneurtAnim 1s 1.7s forwards ease-out;
    }
    @keyframes enterpreneurtAnim{
        to{
            opacity: 1;
            letter-spacing: .4rem;
        }
    }
}
